* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins";
}
body {
  margin: 0;
  margin-bottom: 80px;

  background: #fcfcfc;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.site-title {
  font-family: "Grand Hotel";
  font-weight: lighter;
  font-size: 5em;
  margin-top: 60px;
}

.search-section {
  width: 60%;
  margin: 0 auto;
  margin-top: 10%;
}
.search-input {
  width: 100%;
  height: 60px;
  padding-left: 30px;
  border-style: None;
  border-radius: 50px;
  background: #fffefc;
  box-shadow: 5px 5px 12px gainsboro;
}
/* Game Card  */
.game-card {
  background: #faf4d7;
  width: 60%;
  margin: 0 auto;

  margin-top: 60px;
  text-align: left;
}
.game-body {
  display: grid;
  color: white;
  grid-template-columns: 1fr 1fr;
  height: 300px;
}

.left-side {
  background: #4c68f5;
  position: relative;
  display: flex;
  align-items: center;

  justify-content: center;
}

.game-body h1 {
  font-size: 1.3rem;
  padding-left: 40px;
  padding-right: 40px;
}

.right-side {
  position: relative;
  background: #f5574c;
  display: flex;

  align-items: center;
  justify-content: center;
}
.game-card-nav {
  background: white;
  border-top: 1px solid gainsboro;
  padding: 20px;
  display: flex;
  align-items: center;

  justify-content: space-between;
}
.card-title {
  font-size: 20px;
}
/* Create Card form */
.create-game-container {
  width: 60%;
  margin: 0 auto;
  margin-top: 40px;
}
.create-game-card {
  background: #f0efed;
}
.create-text {
  font-size: 1.2rem;
  opacity: 0.3;

  padding: 40px;
}
.fa-plus-square {
  font-size: 40px;
  opacity: 0.3;
}
.hidden {
  transition: 0.5s all ease-in-out;
  display: none;
}
.show {
  animation: reveal 0.2s ease-in-out;
  display: grid;
}

#username {
  color: blue;

  position: relative;
}

.create-game-body {
  margin-top: 30px;
  position: absolute;
  z-index: 5;

  min-width: 600px;
  width: 70%;

  grid-template-columns: 1fr 1fr;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #fffefc;
}
.create-btn {
  width: 100%;
  grid-column: 1/4;
  background: #6ad4a9;
  padding: 20px;
  border-style: None;
}
.create-game-form {
  text-align: left;
  background: #8bf09f;

  margin: 0 auto;
  width: 100%;
}
.form-inner {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
}
.exit {
  position: absolute;
  font-size: 23px;
  right: 7%;
  top: 7%;
  color: gainsboro;
}
.create-game-form input {
  display: block;

  width: 80%;
  margin: 0 auto;

  background: #fffefc;

  border-style: None;
  padding: 20px;
  margin-top: 20px;
}
.game-preview {
  width: 90%;
  margin: 0 auto;
  color: white;
  background: #f0efed;
  display: grid;
  height: 200px;
  font-weight: bold;
  margin-top: 40px;
  margin-bottom: 40px;
  grid-template-columns: 1fr 1fr;
}
.prev-left,
.prev-right {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 35px;
}

.choose-col {
  margin-top: -10px;
}

.game-selection {
  padding: 40px;
  position: relative;
}
/* Color selection nav */
.color-selection {
  width: 80%;
  margin: 0 auto;

  display: flex;
  align-items: center;
  justify-content: center;
}
.color-split {
  background: gainsboro;
  width: 2.4vw;
  height: 2.4vw;
  border-radius: 50px;
  margin-top: 20px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  overflow: hidden;
}

.color-split {
  border: 3px solid white;
  box-shadow: 3px 3px 3px gainsboro;
}

.isActive {
  border: 10px solid white;
  width: 50px;
  height: 50px;
}
.vote-count {
  font-size: 1.3em;
  border: 3px solid #fcfcfc;
  width: 100px;
  color: white;
  height: 100px;

  border-radius: 209px;

  position: absolute;
}
.count-right {
  position: absolute;
  left: -6%;
  bottom: -6%;
}

.count-left {
  position: absolute;
  right: -6%;
  bottom: -6%;
}
.vote {
  display: flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  font-weight: bold;
}
.color-split:not(:first-child) {
  margin-left: 20px;
}
.red-col {
  background: #f5574c;
}
.blue-col {
  background: #4c68f5;
}
.gold-col {
  background: #ffc859;
}
.dark-col {
  background: #141312;
}
.green-col {
  background: #6ad4a9;
}
.lblue-col {
  background: #44b0eb;
}
@keyframes reveal {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  75% {
    opacity: 0.7;
  }
  100% {
    opacity: 1;
  }
}

/* media queries */

@media screen and (max-width: 1000px) {
  .create-game-body {
    width: 86%;
  }

  .search-section,
  .game-card,
  .create-game-container {
    width: 80%;
  }
  .site-title {
    font-size: 64px;
  }
}
@media screen and (max-width: 600px) {
  .site-title {
    font-size: 50px;
    margin-bottom: -10px;
  }
  .search-section {
    width: 80%;
  }
  .search-section input {
    height: 3.5em;
  }
  .card-title {
    font-size: 1em;
  }
  .creator {
    font-size: 0.7em;
  }
  .game-body {
    grid-template-columns: 1fr;
  }
  .vote-count {
    top: 15%;
    width: 70px;
    height: 70px;
  }
}
